<template>
    <div id="header">
        <Avatar />
    </div>   
 </template>

<script>
import Avatar from './header/Avatar'

export default{
    components: {
        Avatar,
    }
}
</script>

<style lang="stylus" scoped>
#header{
    height 230px
    width 100%
    position absolute
    top 0
    background #2a2a2a
}
</style>




