<template>
    <div id="photo">
        <div id="container">
            <div class="item"> <img src="@/assets/photos/1.jpg" alt="" /> </div>
            <div class="item"> <img src="@/assets/photos/2.jpg" alt="" /> </div>
            <div class="item"> <img src="@/assets/photos/3.jpg" alt="" /> </div>
            <div class="item"> <img src="@/assets/photos/4.jpg" alt="" /> </div>
            <div class="item"> <img src="@/assets/photos/5.jpg" alt="" /> </div>
            <div class="item"> <img src="@/assets/photos/6.jpg" alt="" /> </div>
        </div>
    </div>
</template>

<script>


export default {


    mounted(){
        this.waterFall()
        
    },


    methods: {
    

        waterFall: function() {
            var box = document.getElementById('photo');
            var items = document.getElementsByClassName('item');
            // 定义每一列之间的间隙 为10像素
            var gap = 10;
            // 列数  = 页面的宽度 / 图片的宽度
            var pageWidth = box.offsetWidth;
            console.log(pageWidth)
            var itemWidth = items[0].offsetWidth;
            var columns = parseInt(pageWidth / (itemWidth + gap));
            console.log(itemWidth)
            console.log(columns)
            var arr = [];
            for (var i = 0; i < items.length; i++) {
                if (i < columns) {
                    // 2- 确定第一行
                    items[i].style.top = 0;
                    items[i].style.left = (itemWidth + gap) * i + 'px';
                    arr.push(items[i].offsetHeight);

                } else {
                    // 其他行
                    // 3- 找到数组中最小高度  和 它的索引
                    var minHeight = arr[0];
                    var index = 0;
                    for (var j = 0; j < arr.length; j++) {
                        if (minHeight > arr[j]) {
                            minHeight = arr[j];
                            index = j;
                        }
                    }
                    // 4- 设置下一行的第一个盒子位置
                    // top值就是最小列的高度 + gap
                    items[i].style.top = arr[index] + gap + 'px';
                    // left值就是最小列距离左边的距离
                    items[i].style.left = items[index].offsetLeft + 'px';

                    // 5- 修改最小列的高度 
                    // 最小列的高度 = 当前自己的高度 + 拼接过来的高度 + 间隙的高度
                    arr[index] = arr[index] + items[i].offsetHeight + gap;
                }
            }
        },

    }
}
</script>


<style lang="stylus" scoped>
#photo{
    width 100%
    height 100%
    position absolute
    top 100px
    #container{
    }
}   
    img {
        width: 300px;
        display: block;
    }
    
    .item {
        box-shadow: 2px 2px 2px #999;
        position: absolute;
    }
</style>



