<template>
    <div id="home">
        <div id="atc">
            <router-view />
            <div class="article" id="article1">
                <div class="title"> <a @click="article('1906202')">第二篇文章</a> </div>
                <div class="details"><span>发布时间:2019-6-20</span></div>
            </div>
            <div class="article" id="article2">
                <div class="title"> <a @click="article('1906201')">第一篇文章</a> </div>
                <div class="details"> <span>发布时间:2019-6-20</span></div>
            </div>  
        </div>
    </div>
</template>

<script>


export default {
    methods: {
        article: function(aid){
            this.$router.push({name: 'Article', 
                params: {artId: aid} 
            })
        },
    },
}
</script>

<style lang="stylus" scoped>
#home{
    height 100%
    width 80%
    margin-left 10%
    position absolute
    top 100px
}
#atc{
    width 100%
    margin 0 auto
}
.article{
    width 100%
    height 50px
    margin-top 20px
    margin-left 0
    border-bottom dashed
    border-width 1px 
    border-color lightgray
    
    .title{
        width 100%
        a{
            position absolute
            left 0
            cursor pointer
            font-size 19px
            font-weight bold
        }
        a:hover{
            color navy
        }
    }

    .details{
        width 100%
        span{
            float right
            margin-left 20px
            font-size 15px
        }
    }

}
</style>



